<template>
    <div>
  
      <h1 class="pb-2">
       <icons :icon="['fas', 'award']" /> 
       <strong>NOVEDADES</strong>
       <icons :icon="['fas', 'award']" /> 
       </h1>  
                  
        <div class="container-md">
            <b-card-group columns>
              <div columns v-for="noticia in noticias" :key="noticia.id">
               <CardNovetats :autor="noticia.autor" :srcimg="noticia.srcimg" :alt="noticia.alt" :titulo="noticia.titulo" :texto="noticia.texto" :url="noticia.url"></CardNovetats> 
              </div>
             </b-card-group>
         </div>
  
  
   </div>
  
  </template>
  
  <script>
  import CardNovetats from '../components/CardNovetats.vue' 
  //(para utilizar en localhos)
  //const lstProysJson = '/public/../proyectosWD2021.json';
  //para utilizar en produccion
  const lstProysJson = 'https://raw.githubusercontent.com/ccm24/rock_records/main/NoticiasRock.json';
  
  export default {
    props: {  
     name: {
        type: String,
        default: 'NovetatsRock'
      }
    },
    components: {
        CardNovetats
        },
    data() {
        return {
            noticias:[],
          githubPage: 1,
          page: 1,
          loading: false,
          perPage: 5
        }
      },
      mounted() {
        this.fetchData()
      },

      methods: {
          async fetchData() {
            const res = await fetch (lstProysJson);
            const val= await res.json();
            this.noticias=val;
            console.log(val);
          }
      }
    }
  </script>
  